import { min2EOS } from './unitConvert';

/**
 * @usage:
 *  @input: 12345678901.1234
 *  @output: 12345678901.123
 **/

const LEN = 8;
export const formatEOS = (num, isMin) => {
  // 最小单位转换
  const v = isMin ? min2EOS(num) : num;
  // console.log(v, 'v');

  // 计算fixed保留位数：正数位+浮点位 <= 12, 保留4浮点；否则长度每增加一位，减少一位浮点
  const len = (parseInt(v) + '').length;
  let fixedLen = Math.max(len > 4 ? LEN - len : 4, 0);
  // console.log(fixedLen, 'fixedLen')

  const v1 = parseFloat(v.toFixed(fixedLen)) || 0;

  // console.log(v1, 'v1')
  // 整数部分加千位','
  return formatNumber(v1);
};

/**
 * 1000.1234 => 1,000.1234
 **/
export const formatNumber = (num) => {
  const parts = num.toString().split('.');
  const reverseNum = parts[0].split('').reverse().join('');
  const formatted = reverseNum.replace(/\d{3}(?=[^$])/g, '$&,');
  if (parts[1]) {
    return formatted.split('').reverse().join('') + '.' + parts[1];
  }
  return formatted.split('').reverse().join('');
};


export const formatDate = (timestamp) => {
  const time = new Date(timestamp);
  const year = time.getFullYear();
  const month = time.getMonth() + 1;
  const day = time.getDate();
  return {
    year,
    month,
    day
  }
};


export const delay = (ms) =>
  new Promise((resolve) => {
    setTimeout(() => {resolve()}, ms)
  });


export const parseUrl = (url) => {
  let result = [];
  let query = url.split("?")[1];

  if (!query) {
    return result;
  }

  let queryArr = query.split("&") || [];

  queryArr.forEach(function(item){
    let obj = {};
    let value = item.split("=")[1];
    let key = item.split("=")[0];
    obj[key] = value;
    result.push(obj);
  });
  return result;
};
