import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import TimeLeft from '../Parts/TimeLeft/TimeLeft';
import { formatEOS } from "../../Utils/utils";
import i18n from "../../language";
import './RoundInfo.scss'
import {min2EOS} from '../../Utils/unitConvert'

class RoundInfo extends Component {
  render() {
    const { latestWinner, roundStatus, pot } = this.props;
    console.log(this.props, 'ssssdfsdf')
    console.log(roundStatus, 'roundStatus')
    const isGameRunning = roundStatus === 'running';
    const isGameWaiting = roundStatus === 'pause';
    return (
      <div className="round-wrapper">
        {
          <div className="round-content-wrapper">
            <p className={`round-title-bonus`}>
              {isGameRunning ? i18n.t('bonus') : '本轮游戏结束'}
            </p>

            <p className="round-text-bonus"><span className="round-text-bonus-span break-word">{formatEOS(pot, true) || 0}</span></p>

            <p className="text-small-light">{isGameRunning ? i18n.t('count_down') : '下一轮开始时间'}</p>

            {
              isGameWaiting && latestWinner &&
              <div className="round-latest-winner-wrapper">
                <p className="text-medium-light">
                  <img src={require('../../images/win.png')} alt="endless60" />恭喜{latestWinner.winner}获得<span>{min2EOS(latestWinner.amount)} EOS</span>大奖</p>
              </div>
            }
            <TimeLeft />
          </div>
        }
      </div>
    )
  }
}

function mapStateToProps(state) {
  const { rankList = [] } = state.rankInfo;
  return {
    language: state.language,
    pot: state.roundInfo.pot,
    roundStatus: state.roundInfo.status,
    latestWinner: rankList[0]
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(RoundInfo);
