import React, { Component } from 'react';
import { createPortal } from 'react-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import LanguagePicker from '../Parts/LanguagePicker/LanguagePicker';
import _debounce from 'lodash/debounce';
import {login, logout, updateUnformedUpstreamPlayer} from '../../actions';
import { shareMyLink } from '../../actions/postMessage';
import i18n from '../../language';
import './header.scss';
import { initApp } from '../../actions';
import {parseUrl} from "../../Utils/utils";


class Header extends Component {
  state = {
    openMenu: false,
    showLogout: false
  };

  componentDidMount() {
    // 在redux-persist后初始化
    this.props.initApp();

    // 监听滚动
    this.scrollListener();

    // 处理深度链接推荐人参数
    const { uid } = parseUrl(window.location.href) && parseUrl(window.location.href)[0] || {};

    if (uid) {
      this.props.updateUnformedUpstreamPlayer(uid);
    }
  }

  scrollListener = () => {
    const header = document.querySelector('.header-wrapper');
    const scroller = document.querySelector('.App-container');
    const shareBtn = document.querySelector('.fixed-share-btn');
    // header.style.backgroundColor = `rgba(18,15,27,1)`;

    const handleOpacity = _debounce(() => {
      shareBtn.style.opacity = 1;
    }, 500);

    // todo: 兼容uc qq
    scroller.onscroll = (dom) => {

      // header栏目透明度
      const offset = 100;
      if (dom.target.scrollTop > offset) {
        header.style.transition = 'background-color 0.8s';
        header.style.backgroundColor = `rgba(18,15,27,1)`;
      }

      header.style.backgroundColor = `rgba(18,15,27,${dom.target.scrollTop / offset})`;

      // 悬浮按钮透明度
      shareBtn.style.opacity = 0;
      handleOpacity();
    };
  };

  toggle = () => {
    this.setState(({ openMenu }) => ({ openMenu: !openMenu }))
  };

  toggleLogout = (showLogout) => {
    this.setState({showLogout})
  };

  logout = () => {
    this.props.logout();
    this.toggleLogout(false)
  };

  render() {
    const { identity } = this.props;
    const { openMenu } = this.state;
    return [
      <Layout.Row key="header-part-1" className="header-wrapper">
        <Layout.Col xs="4" sm="4" md="4" lg="0" className="header-btn-box">
          <div onClick={this.toggle} className="header-menu-btn">
            <img className="header-img-menu" src={openMenu ? require('../../images/menu_close.png') : require('../../images/menu.png')} alt="" />
          </div>
        </Layout.Col>
        {
          createPortal(
            (
              <div onClick={this.toggle} className={openMenu ? 'header-modal-active' : 'header-modal-inactive'}>
                <div onClick={(e) => e.stopPropagation() } className={openMenu ? 'header-menu-content-active' : 'header-menu-content'}>
                  {/*<p className="header-link" onClick={() => {}}>{i18n.t('contract_us')}</p>*/}
                  <LanguagePicker />
                </div>
              </div>
            ),
            document.getElementById('root')
          )
        }
        <Layout.Col xs="12" sm="12" md="12" lg="12" className="header-logo-box">
          <img className="header-logo" src={require('../../images/60s.png')} alt=""/>
          <span className="title-subhead-light">Endless 60s</span>
        </Layout.Col>
        <Layout.Col xs="8" sm="8" md="8" lg="12" className="header-btn-drop-box">
          <div className="login-wrapper">
            {
              identity ?
                <Button size="mini" type="text" onClick={() => this.toggleLogout(true)}>
                  <span className="text-small-light">{identity.name}</span>
                  <img className="header-menu-drop-down" src={require('../../images/arrow_down.png')} alt=""/>
                </Button> :
                <Button size="mini" type="text" onClick={this.props.login}>
                  <span className="text-small-light">{i18n.t('login')}</span>
                </Button>
            }
            {
              this.state.showLogout &&
              <Button className="login-out-btn" size="mini" type="danger" onClick={this.logout}>
                <span className="text-small-light">{i18n.t('logout')}</span>
              </Button>
            }
            {
              this.state.showLogout &&
              createPortal(
                <div onClick={() => this.toggleLogout(false)} className="header-modal" />,
                document.getElementById('root')
              )
            }
          </div>
        </Layout.Col>
      </Layout.Row>,
      <img
        key='header-part-2'
        className={`fixed-share-btn ${identity ? '' : 'hidden' }`}
        src={require('../../images/share_icon.png')}
        onClick={this.props.shareMyLink}
        alt=""
      />
    ]
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    identity: state.identity
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({login, logout, initApp, shareMyLink, updateUnformedUpstreamPlayer}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
