import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import Message from '../Parts/Message/Message';
import Popover from '../Parts/Popover/Popover';
import Player from '../Parts/Player/Player';
import { contractAction, updateWalletInfo } from '../../actions';
import { shareMyLink } from '../../actions/postMessage';
import { getKeyProfit, getEstimateProfit } from '../../Utils/getProfit';
import { min2EOS, min2key } from '../../Utils/unitConvert';
import { key2EOS } from "../../Utils/key2EOS";
import i18n from '../../language';
import './Tabs.scss';

const WITH_KEY_DRAW = 'WITH_KEY_DRAW';

class Me extends Component {
  withdraw = () => {
    if (!this.props.identity) {
      Message('not_login', { type: 'error' });
      return;
    }

    this.props.updateWalletInfo(WITH_KEY_DRAW, true);
    this.props.contractAction(this.props.contract[0], 'withdraw', {
      user: this.props.identity.name,
    })
      .then(res => {
        Message('withdraw_success', { type: 'success' });
        this.props.updateWalletInfo(WITH_KEY_DRAW, false);
      })
      .catch(err => {
        console.log(err, 'withdraw_fail');
        Message('withdraw_fail', { type: 'error' });
        this.props.updateWalletInfo(WITH_KEY_DRAW, false);
      });
  };

  render() {
    const { walletInfo, identity } = this.props;
    const { myBonus, playerKeys = 0, mask: playerMask, lockMask: playerMaskLocked, historyBalance, upstreamPlayer, eos: playerEOS = 0 } = this.props.playerInfo;
    const { eos, mask: roundMask, lockMask: roundMaskLocked, baseValue, rootPlayer, roundKeys } = this.props.roundInfo;
    const { downstreamList = [], isUnlocked, lockValue: affiliateLockValue, unlockedQuantity, lockedValue } = this.props.affiliateInfo;

    const keyProfit = getKeyProfit(roundMask, playerMask, playerKeys, baseValue);
    const keyProfitLocked = getKeyProfit(roundMaskLocked, playerMaskLocked, playerKeys, baseValue) || 0;

    const affProfit = void (0) || 0;
    const affProfitLocked = void (0) || 0;

    const totalProfit = min2EOS(keyProfit + affProfit + historyBalance); //
    const totalAffProfitLocked = min2EOS(keyProfitLocked + affProfitLocked);

    const isBindUpstream = upstreamPlayer && upstreamPlayer !== rootPlayer; // 是否绑定

    // 开奖后奖励
    const estimateProfit = getEstimateProfit(eos, unlockedQuantity, isUnlocked);

    const isFetchingWithdraw = walletInfo.actionName === WITH_KEY_DRAW && walletInfo.isWalletOccupied;

    // 完成推荐预计奖励

    const profitAd = min2EOS(keyProfitLocked) + (min2EOS(playerEOS) * 0.15) + ((key2EOS(roundKeys, 500) || 0) * 0.1);

    return (
      <div className="tabs-wrapper no-left">
        <div className="tabs-me-card-content-wrapper tabs-small-bottom-gap">
          <Layout.Row className="tabs-me-text-summary tabs-small-bottom-gap">
            <Layout.Col className="align-left" span="12">
              <span className="tabs-me-title-my-keys">持有的</span>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="tabs-me-text-my-keys">{min2key(playerKeys)}</span>
            </Layout.Col>
          </Layout.Row>
          <div className="line-light-m tabs-small-bottom-gap" />
          <div className="tabs-me-text-summary-1">
            <div>
              <Popover popperClass="popover-history-profit" width="88" placement="right" content={i18n.t('help_history_profit')}>
                <span className="tabs-me-text-my-profit">累计分红</span>
              </Popover>
            </div>
            <span className="theme-color">{parseFloat(totalProfit.toFixed(4)) || 0}</span><span className="unit-small-theme">EOS</span>
            <span className="vertical-moulding">|</span>
            <span className="tabs-me-text-profit-locked-total">{parseFloat(totalAffProfitLocked.toFixed(4)) || 0}</span>
          </div>
        </div>

        <div className="tabs-me-card-content-wrapper tabs-small-bottom-gap">
          {
            // myBonus != undefined && myBonus !== 0 &&
            // <Layout.Row className="tabs-small-bottom-gap">
            //   <Layout.Col span="12">
            //     <p className="text-medium-light">我的中奖</p>
            //   </Layout.Col>
            //   <Layout.Col className="align-right" span="12">
            //     <span className="title-small-light align-right">{min2EOS(myBonus) || 0}</span>
            //     <span className="unit-small-light">EOS</span>
            //   </Layout.Col>
            // </Layout.Row>
          }
          <Layout.Row className="">
            <Layout.Col span="8">
              <span className="text-medium-light">本轮</span>
              <img className="img-key-in-text" src={require('../../images/key_white.png')} alt=""/>
              <span className="text-medium-light">分红</span>
            </Layout.Col>
            <Layout.Col className="align-right" span="16">
              <span className="title-small-light align-right">{min2EOS(keyProfit) || 0}</span>
              <span className="unit-small-light">EOS</span>
            </Layout.Col>
          </Layout.Row>
          <p className="tabs-me-text-profit-locked-round">{min2EOS(keyProfitLocked) || 0}</p>
          <Layout.Row className="">
            <Layout.Col span="8">
              <Popover placement="right" content={i18n.t('help_history_not_withdraw_profit')}>
                <p className="tabs-me-history-profit">历史分红</p>
              </Popover>
            </Layout.Col>
            <Layout.Col className="align-right" span="16">
              <span className="title-small-light align-right">{min2EOS(historyBalance) || 0}</span>
              <span className="unit-small-light">EOS</span>
            </Layout.Col>
          </Layout.Row>
        </div>

        <div className="tabs-me-card-content-wrapper">
          <Layout.Row className="">
            <Layout.Col span="12">
              <p className="text-medium-light">开奖后分红（预估）</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="title-small-light">{ parseFloat(estimateProfit.toFixed(4)) || 0 }</span>
              <span className="unit-small-light">EOS</span>
            </Layout.Col>
          </Layout.Row>
        </div>

        <div onClick={this.props.shareMyLink} className="tabs-me-join-wrapper">
          <img className="tabs-img-join" src={require('../../images/share.png')} alt="" />
          <span className={`tabs-title-join ${!isBindUpstream ? 'tabs-title-join-newer' : ''}`}>推荐好友赢取奖励</span>
          { isBindUpstream && <p className="tabs-me-text-subordinates">我的推荐人：{upstreamPlayer}</p> }
        </div>
        <div className="tabs-me-card-content-wrapper">
          <Layout.Row className="">
            <Layout.Col span="8">
              <p className={`tabs-me-subordinates-profit ${isUnlocked ? 'subordinates-unlocked': ''}`}>直推奖励</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="16">
              <span className="title-small-light theme-color">{min2EOS(lockedValue) || 0}</span>
              <span className="unit-small-theme">EOS</span>
            </Layout.Col>
          </Layout.Row>
          <Popover placement="right" content={i18n.t('help_withdraw')}>
            <p className="tabs-me-text-withdraw-help">如何提取</p>
          </Popover>
          <Layout.Row className="tabs-small-bottom-gap">
            <Layout.Col span="16">
              <p className="title-small-light">我推荐的玩家</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="8">
              <span className="title-medium-light theme-color">{downstreamList.length}</span>
            </Layout.Col>
          </Layout.Row>
          <div className="line-light-m tabs-small-bottom-gap" />

          <div className="tabs-me-subordinates-list-wrapper">
            {
              downstreamList
                .map((d, i) => <Player key={i + 'subordinates'} name={d.name} invest={d.keys} commission={`+${min2EOS(d.profit)}`} />)
            }
          </div>
          {
            !isUnlocked &&
              <div onClick={this.props.shareMyLink} className="tabs-me-join-wrapper-ad">
                <p className="tabs-title-join">推荐条件达成后可获得</p>
                <p className="tabs-me-text-subordinates-ad">{parseFloat(profitAd.toFixed(4))} EOS</p>
                <img className="tabs-img-join" src={require('../../images/share2.png')}  alt="" />
              </div>
          }
        </div>
        <Button
          className="tabs-btn-withdraw-dividends"
          type="info"
          loading={isFetchingWithdraw}
          disabled={!isFetchingWithdraw && (walletInfo.isWalletOccupied || !identity)}
          onClick={this.withdraw}
        >
          取出分红
        </Button>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    contract: state.contract,
    walletInfo: state.walletInfo || {},
    roundInfo: state.roundInfo || {},
    playerInfo: state.playerInfo || {},
    affiliateInfo: state.affiliateInfo,
    identity: state.identity
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({contractAction, updateWalletInfo, shareMyLink}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Me);
