import {
  UPDATE_IDENTITY,
  CHANGE_LANGUAGE,
  UPDATE_ROUND_INFO,
  UPDATE_TOKEN_INFO,
  UPDATE_PLAYER_INFO,
  CLEAR_PLAYER_INFO,
  UPDATE_AFFILIATE_INFO,
  UPDATE_WALLET_INFO,
  UPDATE_UNFORMED_UPSTREAM_PLAYER,
  UPDATE_ACCOUNT_RECORD,
} from '../actions';
import {UPDATE_REMOTE_TIME, UPDATE_TIME_LEFT, UPDATE_RANK_INFO, UPDATE_ACCOUNT_BALANCE} from '../actions/time';
import {UPDATE_MODAL} from '../actions/modal';
import i18n from '../language';

export const initialState = {
  i18n,
  language: void (0),
  socialContact: 'https://t.me/joinchat/Esi1OkPktgcFeJ3Lmlcrqp',
  unformedUpstreamPlayer: void (0), // 未成既定关系的上游玩家
  // bingoShowedRecord: 0, // 已经展示过的我的中奖轮数
  contract: ['helix1234511', 'helix1234514'], //['prdcontract2', 'prdttestiiii'], //
  identity: null,
  EOSBalance: void (0),
  timeOffset: 0, // 服务器时间校正
  roundInfo: {
    fetchStatus: void (0),
    status: void (0), // [pause, running]
    baseValue: 1000 * 1000,
    endTime: void (0),
    startTime: void (0),
    timeLeft: void (0),
    round: void (0),
    eos: void (0),
    total: void (0), // 历史投资的eos
    pot: void (0), // 奖池
    roundKeys: void (0),
    mask: void (0),
    lockMask: void (0),
    tokenAmount: void (0), // TTC分红基数
    rootPlayer: 'helix1234514', // 官方玩家名
    minInvestment: 1, // 最小投资key
  },
  playerInfo: {
    fetchStatus: void (0),
    playerKeys: void (0),
    mask: void (0),
    lockMask: void (0),
    historyBalance: void (0), // 历史分红
    upstreamPlayer: void (0), // 上家
    myBonus: void (0), // 我的大奖分红
  },
  tokenInfo: {
    fetchStatus: void (0),
    balance: void (0), // token余额
    payoutsTo: void (0), // token支出
    symbol: 'HLK',
    table: 'stat',
    maxSupply: 10000000
  },
  affiliateInfo: {
    fetchStatus: void (0),
    isUnlocked: false,
    downstreamList: [],
    lockValue: void (0),
    unlockedQuantity: void (0)
  },
  rankInfo: {
    // latestIndex: 0, // 历史交易增量点
    fetchStatus: void (0),
    rankList: [], // rid: 1, winner: "lcbsss123455", amount: 39, rank: Array(0)
  },
  modal: {
    open: false,
    types: ['rank-list', 'bind-up-prompt', 'bingo'],
    type: void (0),
    data: void (0)
  },
  // 钱包调用信息
  walletInfo: {
    actionName: void (0),
    isWalletOccupied: false
  },
  // 多用户存储
  testAccount: {
    bingoShowedRecord: 0 // 已经展示过的我的中奖轮数
  }
};

export default function (state = initialState, action) {
  switch (action.type) {
    case CHANGE_LANGUAGE:
      return {...state, language: action.payload.language};
    case UPDATE_UNFORMED_UPSTREAM_PLAYER:
      return {...state, unformedUpstreamPlayer: action.payload.unformedUpstreamPlayer};
    case UPDATE_IDENTITY:
      return {...state, identity: action.payload.identity};
    case UPDATE_REMOTE_TIME:
      return {...state, timeOffset: action.payload.timeOffset};
    case UPDATE_TIME_LEFT:
      return {...state, timeLeft: action.payload.timeLeft};
    case UPDATE_ROUND_INFO:
      return {...state, roundInfo: {...state.roundInfo, ...action.payload}};
    case UPDATE_TOKEN_INFO:
      return {...state, tokenInfo: {...state.tokenInfo, ...action.payload}};
    case UPDATE_PLAYER_INFO:
      return {...state, playerInfo: {...state.playerInfo, ...action.payload}};
    case CLEAR_PLAYER_INFO:
      return {
        ...state,
        playerInfo: {...initialState.playerInfo},
        EOSBalance: initialState.EOSBalance,
        affiliateInfo: initialState.affiliateInfo
      };
    case UPDATE_AFFILIATE_INFO:
      return {...state, affiliateInfo: {...state.affiliateInfo, ...action.payload}};
    case UPDATE_RANK_INFO:
      return {...state, rankInfo: {...state.rankInfo, ...action.payload}};
    case UPDATE_MODAL:
      return {...state, modal: {...state.modal, ...action.payload}};
    case UPDATE_WALLET_INFO:
      return {...state, walletInfo: {...state.walletInfo, ...action.payload}};
    case UPDATE_ACCOUNT_RECORD:
      return {...state, [state.identity.name]: { ...state[state.identity.name], ...action.payload }};
    case UPDATE_ACCOUNT_BALANCE:
      return {...state, EOSBalance: action.payload.EOSBalance };
    default:
      return state;
  }
};
