/**
 * 推荐人填写暂存
 **/
import React, {useState} from 'react';
import Button from 'element-react/dist/npm/es5/src/button';
import Input from 'element-react/dist/npm/es5/src/input';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {transfer, updateUnformedUpstreamPlayer} from '../../actions';
import './MsgBox.scss';
import i18n from '../../language';


const RoundRankList = (props) => {
  const [value, setValue] = useState(props.unformedUpstreamPlayer);

  const saveUpstream = () => {
    props.updateUnformedUpstreamPlayer(value);
    props.close(true);
  };

  return (
    <div className="modal-content-up-bind">
      <i className="el-icon-close modal-btn-close" onClick={props.close} />
      <p className="modal-up-bind-title">填写推荐人</p>
      <Input
        type='text'
        placeholder="请填写推荐人"
        value={value}
        onChange={setValue}
      />
      <Button
        className="modal-up-bind-cancel"
        onClick={() => props.close(true)}
      >
        取消
      </Button>
      <Button
        className="modal-up-bind-save"
        onClick={saveUpstream}
      >
        保存
      </Button>
    </div>
  )
};

function mapStateToProps(state) {
  return {
    language: state.language,
    unformedUpstreamPlayer: state.unformedUpstreamPlayer,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({transfer, updateUnformedUpstreamPlayer}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(RoundRankList);

