import React, { useState } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from "redux";
import Layout from 'element-react/dist/npm/es5/src/layout';
import { formatEOS } from '../../Utils/utils';
import { updateModal } from '../../actions/modal';
import './Tabs.scss';
import i18n from '../../language';


const Rank = (props) => {
  const [isFold, setFold] = useState(false);

  const getRoundName = (rid) => {
    if (rid === props.round - 1) {
      return '上一轮大奖'
    }
    return `第${rid}轮大奖`
  };
  if (props.rankList.length === 0) {
    return null
  }

  return (
    <div className="tabs-wrapper">
      <div
        onClick={() => {
          setFold(!isFold)
        }}
      >
        <Layout.Row className="">
          <Layout.Col span="12" className="align-left">
            <p className="title-medium-light">获奖统计</p>
          </Layout.Col>
          <Layout.Col span="12" className="align-right">
            <p className={`tabs-rank-arrow el-icon-arrow-up ${isFold ? 'is-fold' : ''}`} />
          </Layout.Col>
        </Layout.Row>
      </div>

      <div className={`tabs-rank-wrapper ${isFold ? 'is-fold': ''}`}>
        {
          props.rankList.map((d, i) => {
            return (
              <div key={`rank-item-${i}`} className="rank-item-wrapper">
                <p className="tabs-rank-item-title">{getRoundName(d.rid)}</p>
                <p className="tabs-rank-text-winner">{d.winner}</p>
                <div className="title-medium-light align-center tabs-small-bottom-gap theme-color">
                  {formatEOS(d.amount, true)}
                  <span className='unit-small-theme'>EOS</span>
                </div>
                {
                  d.rank[0] &&
                  <p className="tabs-rank-text-honor">赚钱达人No.1</p>
                }
                {
                  d.rank[0] &&
                  <p className="text-medium-light align-center tabs-small-bottom-gap">{d.rank[0].first}</p>
                }
                {
                  d.rank[0] &&
                    <p
                      className="tabs-rank-label-top10"
                      onClick={() => {
                        props.updateModal({
                          open: true,
                          type: 'rank-list',
                          data: {index: i}})
                      }}
                    >TOP10</p>
                }
                { i !== props.rankList.length - 1 && <div className="line-light-m tabs-small-bottom-gap" /> }
              </div>
            )
          })
        }
      </div>
    </div>
  )
};

function mapStateToProps(state) {
  return {
    language: state.language,
    round: state.roundInfo.round,
    rankList: /*[{rid: '1', winner: 'testststt', amount: 123, rank: [{amount: 1, name: 'xxxx'}]}] || */state.rankInfo.rankList || [],
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({updateModal}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Rank);
