/**
 * element-react 手机端Popover消失只能点击children问题
 */
import React, { useState } from 'react';
import Popover from 'element-react/dist/npm/es5/src/popover';
import {createPortal} from "react-dom";
import './popover.scss'


export default (props) => {
  const [visible, setVisible] = useState(false);

  return [
    <Popover {...props} key='endless-popover' visible={visible} trigger="manual">
      <span
        onClick={(e) => {
          setVisible(!visible);
          e.stopPropagation();
        }}
      >
        {props.children}
      </span>
    </Popover>
    ,
      visible &&
      createPortal(
        (
          <div
            key="endless-popover-modal"
            onClick={() => setVisible(false)}
            className="endless-popover-wrapper"
          />
        ),
        document.getElementById('root'))
    ]
};
