/**
 * 中奖
 **/
import React from 'react';
import _get from 'lodash/get';
import Button from 'element-react/dist/npm/es5/src/button';
import {connect} from 'react-redux';
import { min2EOS } from '../../Utils/unitConvert';
import i18n from '../../language';
import './MsgBox.scss';


const RoundRankList = (props) => {
  const amount = _get(props.rankList, ['0', 'amount']);

  return (
      <div className="modal-content-bingo">
        <div className="modal-bingo-arc" />
        <p className="modal-bingo-congratulation">恭喜您！获得大奖</p>
        <p className="modal-bingo-amount">{min2EOS(amount)} EOS</p>
        <p className="text-tiny-light-s">奖励会在24小时内打入您的账户</p>
        <Button
          type="primary"
          className="modal-bingo-button"
          onClick={props.close}
        >
          太好了
        </Button>
        <div className="modal-img-win" />
      </div>
  )
};

function mapStateToProps(state) {
  return {
    language: state.language,
    unformedUpstreamPlayer: state.unformedUpstreamPlayer,
    rankList: state.rankInfo.rankList,
  };
}


export default connect(mapStateToProps)(RoundRankList);

