/**
 * 发送消息到rn
 **/

export const shareMyLink = () => {
  return async (dispatch, getState) => {
    const { identity = {} } = getState();
    if (!identity.name) return;

    const data = `快来跟我一起玩游戏吧，人多才好玩http://finderd.app \n 哪里去玩？还没有DF，快下一个吧？http://finderd.app/share?page=endless60&uid=${identity.name}`;

    window.ReactNativeWebView &&
      data &&
      window.ReactNativeWebView.postMessage(JSON.stringify({channel: 'native-share', data}))
  }
};
