import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import Popover from '../Parts/Popover/Popover';
import { getTokenProfit } from '../../Utils/getProfit';
import { contractAction, updateWalletInfo } from '../../actions';
import Message from "../Parts/Message/Message";
import i18n from '../../language';
import './Tabs.scss';

const WITH_TTC_DRAW = 'WITH_TTC_DRAW';
const TTC_REINVEST = 'TTC_REINVEST';

class Profit extends Component {
  withdraw = () => {
    if (!this.props.identity) {
      Message('not_login', { type: 'error' });
      return;
    }

    this.props.updateWalletInfo(WITH_TTC_DRAW, true);
    const { name } = this.props.identity || {};
    this.props.contractAction(this.props.contract[0], 'twithdraw', {
      user: name,
    })
      .then(res => {
        Message('withdraw_success', { type: 'success' });
        this.props.updateWalletInfo(WITH_TTC_DRAW, false);
      })
      .catch(err => {
        console.log(err, 'withdraw_fail');
        Message('withdraw_fail', { type: 'error' });
        this.props.updateWalletInfo(WITH_TTC_DRAW, false);
      });
  };

  ttcReinvest = () => {
    if (!this.props.identity) {
      Message('not_login', { type: 'error' });
      return;
    }

    this.props.updateWalletInfo(TTC_REINVEST, true);
    const { name } = this.props.identity || {};
    this.props.contractAction(this.props.contract[0], 'tinvest', {
      user: name,
    })
      .then(res => {
        Message('ttc复投成功', { type: 'success' });
        this.props.updateWalletInfo(TTC_REINVEST, false);
      })
      .catch(err => {
        console.log(err, 'withdraw_fail');
        Message('ttc复投失败', { type: 'error' });
        this.props.updateWalletInfo(TTC_REINVEST, false);
      });
  };

  render() {
    const { tokenInfo, tokenBalance, identity, tokenAmount, tokenPayoutsTo, walletInfo, roundStatus, tokenSymbol } = this.props;
    const tokenProfit = getTokenProfit(tokenAmount, tokenBalance, tokenPayoutsTo, tokenInfo.maxSupply);

    const isFetchingWithdraw = walletInfo.actionName === WITH_TTC_DRAW && walletInfo.isWalletOccupied;
    const isFetchingReinvest = walletInfo.actionName === TTC_REINVEST && walletInfo.isWalletOccupied;

    // console.log(tokenAmount, tokenBalance, tokenPayoutsTo, baseValue, 'totalProfit');
    return (
      <div className="tabs-wrapper">
        <Popover placement="top-start" width="224" content={i18n.t('help_more_about_HLK')}>
          <p className="tabs-profit-more">了解更多</p>
        </Popover>
        <div className="tabs-game-card-content-wrapper">
          <Layout.Row className="tabs-basic-bottom-gap">
            <Layout.Col span="8">
              <p className="text-medium-light">我持有的{tokenSymbol}</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="16">
              <span className="title-small-light">{ parseFloat(tokenBalance.toFixed(4)) || 0 }</span>
            </Layout.Col>
          </Layout.Row>
          <Layout.Row className="">
            <Layout.Col span="8">
              <p className="text-medium-light">股息分红</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="16">
              <span className="title-small-light theme-color">{ parseFloat(tokenProfit.toFixed(4)) || 0 }</span>
              <span className="unit-small-theme">EOS</span>
            </Layout.Col>
          </Layout.Row>
        </div>
        <Layout.Row>
          <Layout.Col span="10" className="align-left">
            <Button
              type="primary"
              className="tabs-btn-reinvest"
              loading={isFetchingReinvest}
              disabled={roundStatus !== 'running' || !isFetchingReinvest && walletInfo.isWalletOccupied}
              onClick={this.ttcReinvest}
            >
              <span className="tabs-text-with-key">复购</span>
            </Button>
          </Layout.Col>
          <Layout.Col span="10" offset="4" className="align-right">
            <Button
              type="info"
              className="tabs-btn-withdraw-dividends"
              loading={isFetchingWithdraw}
              disabled={!isFetchingWithdraw && (walletInfo.isWalletOccupied || !identity)}
              onClick={this.withdraw}
            >
              取出分红
            </Button>
          </Layout.Col>
        </Layout.Row>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    contract: state.contract,
    roundInfo: state.roundInfo || {},
    identity: state.identity,
    tokenBalance: parseFloat(state.tokenInfo.balance),
    tokenPayoutsTo: parseFloat(state.tokenInfo.payoutsTo),
    tokenAmount: state.roundInfo.tokenAmount,
    roundStatus: state.roundInfo.status,
    tokenInfo: state.tokenInfo,
    tokenSymbol: state.tokenInfo.symbol,
    walletInfo: state.walletInfo,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({contractAction, updateWalletInfo}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Profit);
