import React, { useState } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from "redux";
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import Input from 'element-react/dist/npm/es5/src/input';
import Message from '../Parts/Message/Message';
import { transfer, contractAction, updateWalletInfo } from '../../actions';
import { updateModal } from '../../actions/modal';
import i18n from '../../language';
import { key2EOS } from "../../Utils/key2EOS";
import './Investment.scss';

const INVEST = 'INVEST';
const KEY_REINVEST = 'KEY_REINVEST';

const Investment = (props) => {
  const [value, setValue] = useState('');

  const add = (num) => {
    setValue(parseFloat(value) ? parseFloat(value) + num : num);
  };

  // keys转换成EOS
  const calMoney = (value) => {
    if (props.roundKeys === null || props.roundKeys === undefined) {
      // 没有数据时候预测的价格不对不能购买
      // todo 是否需要考虑currentKeys信息的实时性（监控信息有效性）
      Message('请稍后再试', { type: 'error' });
      return;
    }

    console.log(props.roundKeys, value, 'props.prd, value');
    return key2EOS(props.roundKeys, value);
  };

  const transfer = () => {
    if (!value) {
      Message('请输入有效数量', { type: 'error' });
      return;
    }

    if (value < props.minInvestment)  {
      Message('您购买数量过少', { type: 'error' });
      return;
    }

    props.updateWalletInfo(INVEST, true);

    console.log(props.contractAccount, 'props.contractAccount');

    const isNotBind = !props.upstreamPlayer || props.upstreamPlayer === props.rootPlayer;

    let memo;

    if (props.unformedUpstreamPlayer && props.unformedUpstreamPlayer !== '') {
      memo = props.unformedUpstreamPlayer;
    }

    if (!isNotBind) {
      memo = void (0)
    }

    const eosQuantity = calMoney(value, props.currentKeys);
    if (eosQuantity === undefined) {
      return;
    }

    props.transfer({
      actionName: 'transfer',
      to: props.contractAccount,
      quantity: eosQuantity.toFixed(4) + ' EOS',
      memo: memo ? `:${memo}` : void (0)
    })
      .then(r => {
        console.log(r, 'pay_success');
        setValue('');
        Message('pay_success', { type: 'success' });
        props.updateWalletInfo(INVEST, false);
      })
      .catch(err => {
        let errMsg;
        try {
          errMsg = JSON.parse(err)
        } catch {
          console.warn('parse error');
        }

        console.log(errMsg, 'pay_success11');
        const { error = {} } = errMsg || {};
        const msgKey = error.code === 3050003 ? '推荐人无效' : '支付失败';
        Message(msgKey, { type: 'error' });

        props.updateWalletInfo(INVEST, false);
      });

    console.log('购买', calMoney(value))
  };

  const reinvest = () => {
    // setFetchingStatus(status[2]);
    props.updateWalletInfo(KEY_REINVEST, true);

    props.contractAction(props.contractAccount, 'reinvest', {user: props.identity.name})
      .then(res => {
        Message('复投成功', {type: 'success'});
        // setFetchingStatus(status[0]);
        props.updateWalletInfo(KEY_REINVEST, false);
      })
      .catch(err => {
        console.warn(err);
        Message('复投失败', {type: 'error'});
        props.updateWalletInfo(KEY_REINVEST, false);
        // setFetchingStatus(status[0]);
      });
  };

  let showBindUpstream;
  if (props.playerFetchStatus) {
    showBindUpstream = !props.upstreamPlayer || props.upstreamPlayer === props.rootPlayer; // 是否绑定
  } else {
    showBindUpstream = false
  }

  console.log(showBindUpstream, 'isBindUpstream');

  const isFetchingInvest = props.walletInfo.isWalletOccupied && props.walletInfo.actionName === INVEST;
  const isFetchingKeyReinvest = props.walletInfo.isWalletOccupied && props.walletInfo.actionName === KEY_REINVEST;
  return (
    <div className="summary-wrapper">
      <div className="invest-wrapper">
        <Input
          className="invest-input"
          type='number'
          value={value}
          onChange={setValue}
        />
        <img className="invest-img-key" src={require('../../images/key_black.png')} alt="key" />
        {/*<Button className="btn-add5" type="text">+50</Button>*/}
        <p className="btn-add5" onClick={() => add(50)}>+50</p>
        <p className="btn-add1" onClick={() => add(10)}>+10</p>
        <p className={`invest-balance ${props.EOSBalance ? '' : 'hidden'}`}>账户EOS余额：{props.EOSBalance}</p>

        {
          <p
            className={`invest-text-bind-upstream ${showBindUpstream /*&& props.roundStatus === 'running'*/ ? '' : 'hidden'}`}
            onClick={() => {
              props.updateModal({
                open: true,
                type: 'bind-up-prompt'
              })
            }}
          >填写推荐人</p>
        }

        <Layout.Row>
          <Layout.Col span="12" className="align-left">
            <Button
              className="invest-btn-buy"
              type="primary"
              onClick={transfer}
              loading={isFetchingInvest}
              disabled={props.roundStatus === 'pause' || (!isFetchingInvest && props.walletInfo.isWalletOccupied)}
            >
              <span className="tabs-text-with-key">{i18n.t('btn_buy')}</span>
            </Button>
          </Layout.Col>
          <Layout.Col span="12" className="align-right">
            <Button
              className="invest-btn-rebuy"
              type="info"
              onClick={reinvest}
              loading={isFetchingKeyReinvest}
              disabled={props.roundStatus !== 'running' || !isFetchingKeyReinvest && props.walletInfo.isWalletOccupied}
            >
              {i18n.t('btn_reinvestment')}
              </Button>
          </Layout.Col>
        </Layout.Row>
      </div>
    </div>
  )
};

function mapStateToProps(state) {
  return {
    unformedUpstreamPlayer: state.unformedUpstreamPlayer,
    language: state.language,
    walletInfo: state.walletInfo,
    upstreamPlayer: state.playerInfo.upstreamPlayer,
    rootPlayer: state.roundInfo.rootPlayer,
    eos: state.roundInfo.eos,
    minInvestment: state.roundInfo.minInvestment,
    roundKeys: state.roundInfo.roundKeys,
    currentKeys: state.roundInfo.currentKeys,
    roundStatus: state.roundInfo.status,
    contractAccount: state.contract[0],
    identity: state.identity,
    playerFetchStatus: state.playerInfo.fetchStatus,
    EOSBalance: state.EOSBalance
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({transfer, contractAction, updateWalletInfo, updateModal}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Investment);
